	module.exports = {"data":{"markdownRemark":{"html":"<p>At that time (when I was in high school), a caricature of Ms. Oji (a pseudonym) that Mr. Age was waving a mathematical teaching. I drew it in the image of the brain, not something that was photographed, but I remember being terribly alright when I looked at the face again.</p>\n<p>One piece that I made because I wanted to use a teacher as a character of \" <a href=\"/2898\">JIJIHAZARD</a> \" that was produced by classmate <a href=\"http://mixi.jp/show_friend.pl?id=2308126\">Butterbean</a> at the time. It was Ton Demo setting that the G virus spreads and all the students in the school become Gizza.</p>","frontmatter":{"cover":null,"date":"1997-10-02T00:00:00+09:00","strDate":"1997/10/1","title":"Ms. O"}}},"pathContext":{"path":"/en/blog/1997/10/02-mr-o/","langKey":"en"}}

